import { memo } from "react"
import SelectInput from "../../../Components/SelectInput"

const SelectOptionAgama = ({
    defaultValue,
    name
}: {
    name?: string
    defaultValue?: string
}) => {
    return (
        <SelectInput
            label="Pilih Agama"
            options={[
                {
                    text: "Islam",
                    value: "Islam"
                },
                {
                    text: "Kristen Protestan",
                    value: "Kristen Protestan"
                },
                {
                    text: "Katolik",
                    value: "Katolik"
                },
                {
                    text: "Hindu",
                    value: "Hindu"
                },
                {
                    text: "Buddha",
                    value: "Buddha"
                },
                {
                    text: "Kong Hu Cu",
                    value: "Kong Hu Cu"
                }
            ]}
            id="agama"
            name={name || "agama"}
            defaultValue={defaultValue || "Islam"}
            required
        />
    )
}

export default memo(SelectOptionAgama)
