import { memo } from "react"
import FormGroup from "../../Components/FormGroup"
import FormTextarea from "../../Components/FormTextarea"
import ButtonSubmit from "../Components/Buttons/ButtonSubmit"
import Card from "../../Components/Card"

const AddSku = ({ save }: AddLetterT) => {
    const FormPengajuan = (
        <>
            <div className="row">
                <div className="col-md-6">
                    <FormGroup
                        id="nama_usaha"
                        name="nama_usaha"
                        required
                        label="Nama Usaha"
                        placeholder="Toko Boneka"
                    />
                    <FormTextarea
                        name="alamat_usaha"
                        required
                        label="Alamat"
                        id="alamat_usaha"
                        placeholder="Jl. Pangeran"
                    />
                </div>
                <div className="col-md-6">
                    <FormGroup
                        id="keperluan"
                        name="keperluan"
                        required
                        label="Keperluan Surat"
                        placeholder="melanjutkan pendidikan/pelatihan sertifikasi untuk berlayar"
                    />
                </div>
            </div>

            <ButtonSubmit>Ajukan Surat</ButtonSubmit>
        </>
    )

    return (
        <form onSubmit={save}>
            <Card
                cardHeader={
                    <h4 className="text-info fw-normal">
                        Form Pengajuan Surat Keterangan Usaha
                    </h4>
                }
                cardBody={FormPengajuan}
            />
        </form>
    )
}

export default memo(AddSku)
