import { memo } from "react"

const FormCheck = ({ checks, defaultValue, title, required }: FormCheckT) => {
    return (
        <div className="w-100 mb-5">
            <p className="form-label d-flex justify-content-between">
                {title}
                {required && <span className="text-danger">*</span>}
            </p>

            <div className="d-flex gap-5">
                {checks.map(({ id, label, md, value, ...rest }, i) => {
                    let className = "form-check"
                    if (md) className += ` col-md-${md}`

                    return (
                        <div className={className} key={i}>
                            <input
                                className="form-check-input"
                                type="radio"
                                id={id}
                                value={value}
                                {...rest}
                                defaultChecked={value === defaultValue}
                            />
                            <label className="form-check-label" htmlFor={id}>
                                {label}
                            </label>
                        </div>
                    )
                })}
            </div>
        </div>
    )
}

export default memo(FormCheck)
