import { memo } from "react"
import Card from "../../Components/Card"
import FormGroup from "../../Components/FormGroup"
import FormTextarea from "../../Components/FormTextarea"
import SelectOptionAgama from "../Components/SelectsOption/SelectOptionAgama"
import ButtonSubmit from "../Components/Buttons/ButtonSubmit"

const AddSkck = ({ save }: AddLetterT) => {
    const FormPengajuan = (
        <div className="row">
            <div className="col-md-6">
                <FormGroup
                    type="file"
                    id="surat_pengantar"
                    name="surat_pengantar"
                    required
                    label="Upload Surat Pengantar"
                />
                <FormTextarea
                    name="keterangan"
                    required
                    label="Keterangan Surat Pengantar"
                    id="keterangan"
                    placeholder="Ketua RT. 02 RW. 01 Kelurahan Jembatan Kecil Nomor : 38/ 
                    RT.02-Jk/2017 Tanggal 13 April 2017"
                />
            </div>
            <div className="col-md-6">
                <FormGroup
                    name="keperluan"
                    required
                    label="Keperluan Surat"
                    placeholder="Persyaratan untuk melamar CPNS"
                />
            </div>
        </div>
    )

    const FormOrangTua = (
        <>
            <div className="row">
                <div className="col-md-6">
                    <FormGroup name="nama_ayah" required label="Nama Ayah" />
                    <FormGroup name="nama_ibu" required label="Nama Ibu" />
                </div>
                <div className="col-md-6">
                    <SelectOptionAgama />
                    <FormTextarea
                        name="alamat"
                        id="alamat"
                        label="Alamat"
                        required
                        placeholder="Jl. Mayjen Sutoyo No.30b, RT.002 RW.001, Jemb. Kecil, Kec. 
                        Singaran Pati, Kota Bengkulu, Bengkulu 38223"
                    />
                </div>
            </div>

            <ButtonSubmit>Ajukan Surat</ButtonSubmit>
        </>
    )

    return (
        <form onSubmit={save}>
            <Card
                cardHeader={
                    <h4 className="text-info fw-normal">
                        Form Pengajuan Surat Keterangan Catatan Kriminal
                    </h4>
                }
                cardBody={FormPengajuan}
            />

            <Card
                cardHeader={
                    <h4 className="text-info fw-normal">Form Orang Tua</h4>
                }
                cardBody={FormOrangTua}
            />
        </form>
    )
}

export default memo(AddSkck)
