import { memo } from "react"
import Card from "../../Components/Card"
import FormTextarea from "../../Components/FormTextarea"
import FormGroup from "../../Components/FormGroup"
import SelectOptionAgama from "../Components/SelectsOption/SelectOptionAgama"

const AddSpp: React.FC<AddLetterSppT> = ({ save, pendaftar }) => {
    const FormPengajuan = (
        <div className="row">
            <div className="col-md-6">
                <FormTextarea
                    label="Alamat Pernikahan"
                    name="alamat_pernikahan"
                    id="alamat-pernikahan"
                    rows={6}
                />
            </div>
            <div className="col-md-6 row">
                <div className="col-md-6">
                    <FormGroup label="Tempat Lahir" />
                </div>
                <div className="col-md-6">
                    <FormGroup label="Tanggal Lahir" type="date" />
                </div>
            </div>
        </div>
    )

    const FormOrangTua = (
        <div className="row">
            <h4 className="text-info fw-normal fs-6 mb-4">Ayah</h4>

            <div className="col-md-6">
                <FormGroup label="Nama Ayah" />
                <FormGroup label="NIK Ayah" />
                <div className="row">
                    <div className="col-md-6">
                        <FormGroup label="Tempat Lahir" />
                    </div>
                    <div className="col-md-6">
                        <FormGroup label="Tanggal Lahir" type="date" />
                    </div>
                </div>
            </div>
            <div className="col-md-6">
                <SelectOptionAgama name="agama_ayah" />
                <FormGroup label="Jenis Pekerjaan" />
                <FormTextarea
                    label="Alamat"
                    name="alamat_ayah"
                    id="alamat-ayah"
                    rows={6}
                />
            </div>

            <h4 className="text-info fw-normal fs-6 my-4">Ibu</h4>

            <div className="col-md-6">
                <FormGroup label="Nama Ibu" />
                <FormGroup label="NIK Ibu" />
                <div className="row">
                    <div className="col-md-6">
                        <FormGroup label="Tempat Lahir" />
                    </div>
                    <div className="col-md-6">
                        <FormGroup label="Tanggal Lahir" type="date" />
                    </div>
                </div>
            </div>
            <div className="col-md-6">
                <SelectOptionAgama name="agama_ibu" />
                <FormGroup label="Jenis Pekerjaan" />
                <FormTextarea
                    label="Alamat"
                    name="alamat_ibu"
                    id="alamat-ibu"
                    rows={6}
                />
            </div>
        </div>
    )

    const FormMempelai = (
        <div className="row">
            <div className="col-md-6">
                <FormGroup label="Nama Pasangan Calon Mempelai" />
                <FormGroup label="Nama Pasangan Calon Mempelai" />
                <FormGroup label="Nama Pasangan Calon Mempelai" />
                <div className="row">
                    <div className="col-md-6">
                        <FormGroup label="Tempat Lahir" />
                    </div>
                    <div className="col-md-6">
                        <FormGroup label="Tanggal Lahir" type="date" />
                    </div>
                </div>
            </div>
            <div className="col-md-6">
                <SelectOptionAgama name="agama_mempelai" />
                <FormGroup label="Jenis Pekerjaan" />
                <FormTextarea
                    label="Alamat"
                    name="alamat_mempelai"
                    id="alamat-ibu"
                    rows={6}
                />
            </div>
        </div>
    )

    const FormMantan = (
        <div className="row">
            <div className="col-md-6">
                <FormGroup label="Nama Mantan Pasangan" />
                <FormGroup label="NIK Mantan Pasangan" />
                <FormGroup label="Nama Ayah Mantan Pasangan" />
                <div className="row">
                    <div className="col-md-6">
                        <FormGroup label="Tempat Lahir" />
                    </div>
                    <div className="col-md-6">
                        <FormGroup label="Tanggal Lahir" type="date" />
                    </div>
                </div>
                <SelectOptionAgama name="agama_mantan" />
            </div>
            <div className="col-md-6">
                <FormGroup label="Jenis Pekerjaan" />
                <FormTextarea
                    label="Alamat"
                    name="alamat_mempelai"
                    id="alamat-ibu"
                    rows={6}
                />
                <div className="row">
                    <div className="col-md-6">
                        <FormGroup label="Tempat Wafat" />
                    </div>
                    <div className="col-md-6">
                        <FormGroup label="Tanggal Wafat" type="date" />
                    </div>
                </div>
            </div>
        </div>
    )

    return (
        <form>
            <Card
                cardHeader={
                    <h4 className="text-info fw-normal">
                        Form Pengajuan Surat Keterangan Usaha
                    </h4>
                }
                cardBody={FormPengajuan}
            />

            <Card
                cardHeader={
                    <h4 className="text-info fw-normal">Form Orang Tua</h4>
                }
                cardBody={FormOrangTua}
            />

            <Card
                cardHeader={
                    <h4 className="text-info fw-normal">
                        Calon Pasangan Mempelai
                    </h4>
                }
                cardBody={FormMempelai}
            />

            {pendaftar?.status_perkawinan === "Cerai Mati" && (
                <Card
                    cardHeader={
                        <h4 className="text-info fw-normal">
                            Mantan Suami/Istri Yang Sudah Meninggal
                        </h4>
                    }
                    cardBody={FormMantan}
                />
            )}
        </form>
    )
}

export default memo(AddSpp)
