import { memo } from "react"
import Card from "../../Components/Card"
import FormGroup from "../../Components/FormGroup"
import FormTextarea from "../../Components/FormTextarea"
import FormCheck from "../../Components/FormCheck"
import ButtonSubmit from "../Components/Buttons/ButtonSubmit"

const AddSktm = ({ save }: AddLetterT) => {
    const FormPengajuan = (
        <div className="row">
            <div className="col-md-6">
                <FormGroup
                    label="Upload Surat Pengantar"
                    id="surat-pengantar"
                    name="surat_pengantar"
                    type="file"
                    required
                />
                <FormTextarea
                    id="keterangan"
                    label="Keterangan Surat Pengantar"
                    name="keterangan"
                    required
                    placeholder="Ketua RT. 02 RW. 01 Kelurahan Jembatan Kecil Nomor : 38/ 
                    RT.02-Jk/2017 Tanggal 13 April 2017"
                />
            </div>
            <div className="col-md-6">
                <FormGroup
                    label="Keperluan Surat"
                    name="keperluan"
                    id="keperluan"
                    type="text"
                    required
                    placeholder="Pengajuan Beasiswa Banki Indonesia"
                />
                <FormGroup
                    label="Pendidikan"
                    name="pendidikan"
                    id="pendidikan"
                    type="text"
                    required
                    placeholder="SMKS 15 Taruna Padang Serai Bengkulu"
                />
            </div>
        </div>
    )

    const FormOrangTua = (
        <>
            <div className="row">
                <div className="col-md-6">
                    <FormGroup
                        label="Nama Orang Tua"
                        name="nama_orang_tua"
                        required
                        placeholder="Sidu"
                    />
                    <FormGroup
                        label="NIK"
                        name="nik_orang_tua"
                        type="number"
                        required
                        placeholder="1771032204800001"
                    />
                    <FormGroup
                        label="Status Perkawinan"
                        required
                        name="status_perkawinan"
                        placeholder="Belum Menikah"
                    />
                    <FormCheck
                        title="Jenis Kelamin"
                        checks={[
                            {
                                name: "jenis_kelamin_orang_tua",
                                id: "jenis_kelamin_orang_tua_p",
                                label: "Perempuan",
                                value: "P"
                            },
                            {
                                name: "jenis_kelamin_orang_tua",
                                id: "jenis_kelamin_orang_tua_l",
                                label: "Laki-laki",
                                value: "L"
                            }
                        ]}
                        required
                    />
                    <div className="row">
                        <div className="col">
                            <FormGroup
                                label="Tempat Lahir"
                                name="tempat_lahir_orang_tua"
                                id="tempat_lahir_orang_tua"
                                placeholder="Bengkulu"
                            />
                        </div>
                        <div className="col">
                            <FormGroup
                                label="Tanggal Lahir"
                                name="tanggal_lahir_orang_tua"
                                id="tanggal_lahir_orang_tua"
                                type="date"
                                defaultValue="1980-09-20"
                            />
                        </div>
                    </div>
                </div>

                <div className="col-md-6">
                    <FormGroup
                        label="Pendidikan"
                        name="pendidikan_orang_tua"
                        id="pendidikan_orang_tua"
                        required
                        placeholder="SMA"
                    />
                    <FormGroup
                        label="Pekerjaan"
                        name="pekerjaan_orang_tua"
                        id="pekerjaan_orang_tua"
                        required
                        placeholder="Wirausaha"
                    />
                    <FormTextarea
                        label="Alamat"
                        id="alamat_orang_tua"
                        name="alamat_orang_tua"
                        placeholder="Jl. Mayjen Sutoyo No.30b, RT.002 RW.001, Jemb. Kecil, Kec. 
                        Singaran Pati, Kota Bengkulu, Bengkulu 38223"
                    />
                </div>
            </div>

            <ButtonSubmit>Ajukan Surat</ButtonSubmit>
        </>
    )

    return (
        <form onSubmit={save}>
            <Card
                cardHeader={
                    <h4 className="text-info fw-normal">
                        Form Pengajuan Surat Keterangan Tidak Mampu
                    </h4>
                }
                cardBody={FormPengajuan}
            />

            <Card
                cardHeader={
                    <h4 className="text-info fw-normal">Form Orang Tua</h4>
                }
                cardBody={FormOrangTua}
            />
        </form>
    )
}

export default memo(AddSktm)
