import { useCallback, useEffect, useState } from "react"
import { get, post } from "../../Functions/Api"
import { el } from "../../Functions/GetElement"
import { Link, useNavigate } from "react-router-dom"
import { PopupAlert } from "../../Functions/Alerts"
import SelectInput from "../../Components/SelectInput"
import ButtonBack from "../Components/Buttons/ButtonBack"
import PageTitle from "../Components/PageTitle"
import useUserData from "../../Hooks/useUserData"
import AddSkck from "./AddSkck"
import AddSktm from "./AddSktm"
import AddSku from "./AddSku"
import AddSbm from "./AddSbm"
import AddDomisili from "./AddDomisili"
import AddSpp from "./AddSpp"

const Add = () => {
    const [jenisSurat, setJenisSurat] = useState<SuratT>("Sktm")
    const [anggotaKeluarga, setAnggotaKeluarga] = useState<OptionT[]>([])
    const [dataKeluarga, setDataKeluarga] = useState<DataT[]>()
    const [pendaftar, setPendaftar] = useState<DataT>()
    const [allSurat, setAllSurat] = useState<OptionT[]>([])
    const { no_kk, role } = useUserData()
    const navigate = useNavigate()

    const checkValid = useCallback(() => {
        const jenisSuratE = el("#jenis-surat") as HTMLSelectElement
        const anggotaE = el("#anggota-keluarga") as HTMLSelectElement

        const removeErrorMessage = (el: HTMLElement) => {
            el.classList.remove("is-invalid")
            const messageE =
                el.parentElement?.querySelector(".invalid-feedback")
            if (messageE) messageE.remove()
        }

        const setErrorMessage = (message: string, el: HTMLElement) => {
            if (el.classList.contains("is-invalid")) return

            const messageE = document.createElement("p")
            messageE.classList.add("invalid-feedback")
            messageE.textContent = message

            el.addEventListener("input", () => removeErrorMessage(el))
            el.classList.add("is-invalid")

            el.parentElement?.append(messageE)
        }

        const valid = (() => {
            const isValidJenisSurat = jenisSuratE.value !== label.jenisSurat
            const isValidAnggotaK = anggotaE.value !== label.anggotaK

            if (!isValidAnggotaK) {
                setErrorMessage("Pilih anggota keluarga", anggotaE)
            }
            if (!isValidJenisSurat) {
                setErrorMessage("Pilih jenis surat", jenisSuratE)
            }

            return isValidAnggotaK && isValidJenisSurat
        })()

        if (!valid) window.scrollTo(0, 0)
        return valid
    }, [])

    const navigateToEdit: React.MouseEventHandler<HTMLAnchorElement> = (
        evt
    ) => {
        evt.preventDefault()

        if (checkValid()) {
            const nik = el<HTMLSelectElement>("#anggota-keluarga").value
            localStorage.setItem(
                "current_active_sidebar",
                "/user/data-keluarga"
            )
            navigate(`/${role}/data-keluarga/${no_kk}/${nik}/edit`)
        }
    }

    const label = {
        anggotaK: "Pilih Anggota Keluarga",
        jenisSurat: "Pilih Jenis Surat"
    }

    const handleAnggotaKeluarga: React.FormEventHandler<HTMLSelectElement> = (
        evt
    ) => {
        const pendaftar = dataKeluarga?.find(
            (anggota) => anggota.nik === evt.currentTarget.value
        )
        setPendaftar(pendaftar)
    }

    useEffect(() => {
        const getAnggotaKeluarga = async () => {
            const res = await get(`anggota-keluarga?no_kk=${no_kk}`)
            if (!res.ok) return

            setDataKeluarga(res.data)

            const anggotaKeluarga: OptionT[] = []
            res.data.map((data: DataT) => {
                return anggotaKeluarga.push({
                    text: data.nama,
                    value: data.nik
                })
            })
            setAnggotaKeluarga(anggotaKeluarga)
        }

        const getAllSurat = async () => {
            const res = await get(`info-surat`)
            if (!res.ok) return

            const allSurat: OptionT[] = []
            res.data.map((data: DataT) => {
                if (data.jenis_surat !== "PengantarPernikahan") {
                    return allSurat.push({
                        text: data.nama,
                        value: data.jenis_surat
                    })
                }
            })
            setAllSurat(allSurat)
        }

        getAllSurat()
        getAnggotaKeluarga()
    }, [no_kk])

    const changeJenisSurat = (evt: React.FormEvent<HTMLSelectElement>) => {
        setJenisSurat(evt.currentTarget.value as SuratT)
    }

    const save: React.FormEventHandler<HTMLFormElement> = async (evt) => {
        evt.preventDefault()

        const jenisSuratE = el("#jenis-surat") as HTMLSelectElement
        const anggotaE = el("#anggota-keluarga") as HTMLSelectElement

        if (!checkValid()) return

        const form = evt.currentTarget
        const inputs = [
            {
                name: "letter_type",
                value: jenisSuratE.value
            },
            {
                name: "nik",
                value: anggotaE.value
            }
        ]
        inputs.map(({ name, value }) => {
            const input = document.createElement("input")
            input.type = "hidden"
            input.value = value
            input.name = name

            return form.append(input)
        })

        const res = await post("pengajuan-surat", form)
        if (res.status === 302) {
            localStorage.setItem(
                "current_active_sidebar",
                `/${role}/data-keluarga`
            )
            setTimeout(() => {
                navigate(
                    `/${role}/data-keluarga/${no_kk}/${anggotaE.value}/edit`
                )
            }, 1000)
            return PopupAlert.fire({
                icon: "warning",
                text: res.data.redirect.message ?? ""
            })
        }

        if (res.ok) {
            PopupAlert.fire({
                icon: "success",
                text: "Surat berhasil diajukan."
            })

            setTimeout(() => navigate(`/${role}/ajukan-surat`), 600)
        }
    }

    return (
        <>
            <PageTitle title="Ajukan Surat" />
            <ButtonBack />

            <div className="row px-4 mt-5">
                <div
                    className="alert alert-info alert-dismissible fade show fw-light"
                    role="alert"
                >
                    <strong className="text-danger">Perhatian!</strong> Jika
                    data anda belum lengkap, harap lengkapi data anda telebih
                    dahulu!.{" "}
                    <a
                        className="text-primary text-decoration-underline"
                        onClick={navigateToEdit}
                        href="#"
                    >
                        Klik di sini
                    </a>
                    <button
                        type="button"
                        className="btn-close"
                        data-bs-dismiss="alert"
                        aria-label="Close"
                    ></button>
                </div>
            </div>

            <div className="row my-5">
                <div className="col-md-6">
                    <SelectInput
                        label={label.anggotaK}
                        id="anggota-keluarga"
                        options={anggotaKeluarga}
                        onInput={handleAnggotaKeluarga}
                    />
                </div>
                <div className="col-md-6">
                    <SelectInput
                        label={label.jenisSurat}
                        id="jenis-surat"
                        onInput={changeJenisSurat}
                        options={allSurat}
                    />
                </div>
            </div>

            {jenisSurat === "Skck" && <AddSkck save={save} />}
            {jenisSurat === "Sktm" && <AddSktm save={save} />}
            {jenisSurat === "KeteranganUsaha" && <AddSku save={save} />}
            {jenisSurat === "BelumMenikah" && <AddSbm save={save} />}
            {jenisSurat === "Domisili" && <AddDomisili save={save} />}
            {jenisSurat === "PengantarPernikahan" && (
                <AddSpp save={save} pendaftar={pendaftar} />
            )}
        </>
    )
}

export default Add
