import { memo } from "react"

const FormTextarea: React.FC<FormTextAreaT> = (props) => {
    const { label, name, id, required, ...rest } = props

    return (
        <div className="mb-3">
            <label
                htmlFor={id}
                className="form-label d-flex justify-content-between"
            >
                <span>{label}</span>
                {required && <span className="text-danger">*</span>}
            </label>
            <textarea
                name={name}
                className="form-control"
                id={id}
                rows={3}
                style={{ resize: "none" }}
                {...rest}
            ></textarea>
        </div>
    )
}

export default memo(FormTextarea)
