import { memo } from "react"
import Card from "../../Components/Card"
import FormGroup from "../../Components/FormGroup"
import FormTextarea from "../../Components/FormTextarea"
import ButtonSubmit from "../Components/Buttons/ButtonSubmit"

const AddSbm = ({ save }: AddLetterT) => {
    const FormPengajuan = (
        <>
            <div className="row">
                <div className="col-md-6">
                    <FormGroup
                        type="file"
                        id="surat_pengantar"
                        name="surat_pengantar"
                        required
                        label="Upload Surat Pengantar"
                    />
                    <FormTextarea
                        name="keterangan"
                        required
                        label="Keterangan Surat Pengantar"
                        id="keterangan"
                        placeholder="Ketua RT. 02 RW. 01 Kelurahan Jembatan Kecil Nomor : 38/ 
                        RT.02-Jk/2017 Tanggal 13 April 2017"
                    />
                </div>
                <div className="col-md-6">
                    <FormGroup
                        id="keperluan"
                        name="keperluan"
                        required
                        label="Keperluan Surat"
                        placeholder="Persyaratan untuk melamar kerja"
                    />
                </div>
            </div>

            <ButtonSubmit>Ajukan Surat</ButtonSubmit>
        </>
    )

    return (
        <form onSubmit={save}>
            <Card
                cardHeader={
                    <h4 className="text-info fw-normal">
                        Form Pengajuan Surat Keterangan Belum Menikah
                    </h4>
                }
                cardBody={FormPengajuan}
            />
        </form>
    )
}

export default memo(AddSbm)
